        },
        grid: {
          left: '5%',
          right: '5%',
          bottom: '5%',
          top:'10%'
        },
        xAxis: {
          data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月','8月'],
          //坐标轴
          axisLine: {
            lineStyle: {
              color: '#0A4D6B'
            }
          },
          //坐标值标注
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff',
            }
          }
        },
        yAxis: {
          //坐标轴
          axisLine: {
            show: false
          },
          //坐标值标注
          axisLabel: {
            show: true,
            textStyle: {
              color: '#fff',
            }
          },
