            normal: {
              color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#0B4EC3" // 0% 处的颜色
              }, {
                offset: 0.6,
                color: "#138CEB" // 60% 处的颜色
              }, {
                offset: 1,
                color: "#17AAFE" // 100% 处的颜色
              }], false)
            }
          },
          data: data1,
          barGap: 0
        }, {
          type: 'bar',
          barWidth: 8,
          itemStyle: {
            normal: {
              color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [{
                offset: 0,
                color: "#006A9C" // 0% 处的颜色
