          symbol: 'path://M 0,0 l 120,0 l -30,60 l -120,0 z',
          symbolSize: ['30', '12'],
          symbolOffset: ['-18', '-11'],
          //symbolRotate: -5,
          symbolPosition: 'end',
          data: data1,
